.card{
    background-color: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: 1rem;
    position: absolute;
    left:50%;  /* From left to transform (this 3 lines code is to put errormodal in the center of page)*/
    top:50%;
    transform: translate(-50%, -50%);
    width: 50%; 
    height:45%;
}
