.button{
   font-weight: 400;
   padding:20px 34px; /*top-to-bottom  left-to-right*/
   font-size: 1.3rem;
   /* text-transform: uppercase; */
   background-color: #9370DB;
   color: #fff;
   border: 4px solid #fff;
   font-weight: 600;
   cursor: pointer;
   margin:4px; /*this will add spacing b/w buttons*/
   border-radius: 15px 50px 30px; /*this style will change the shape of button*/
}

.button:hover{
     color:#9370DB;
     transition: 0.1s;
     text-decoration: underline;
    
}
